*
{
  margin: 0;
  padding: 0;
}
body{
  background-color: aliceblue;
  font-family: 'Times New Roman', Times, serif;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
section h1
{
  text-align: center;
  color: goldenrod;
  font-weight: bolder;
  
}
.app{
  background: teal;
  width: 95%;
  min-height: 200px;
  height: min-content;
  border-radius: 15px;
  padding: 20px;
  box-shadow: 10px 10px 42px 0px rgba(0,0,0.75);
  display: flex;
  justify-content: space-evenly;
  font-style: italic;
  margin-top: 30px;

}
.Question-Section{
  position: relative;
  width: 100%;
  color: white;
  font-style: italic;
}
.Question-Section h1 span{
  color: chocolate;
  font-size: 40px;
margin-bottom: 10px;
}
.Question-Text{
  font-size: 30px;
  margin-top: 10px;
  color: khaki;
  font-style: italic;
}
.Answer-Section{
  display: flex;
  flex-direction: column;
  width: 100%;
  justify-content: space-between;
  font-style: italic;
}
button{
  width: 100%;
  font-size: 16px;
  color: black;
  background: ghostwhite;
  border-radius: 15px;
  display: flex;
  padding: 10px;
  justify-content: flex-start;
  align-items: center;
  border: 3px solid grey;
  cursor: pointer;
}
.score-section{
  display: flex;
  align-items: center;
  color:black;
  font-size: 30px;
  font-style: italic;
  font-weight: bold;
}